import {useState} from 'react';
import Layout from '../../../components/Layout';
import Sidebar from '../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faCaretDown, faCaretUp } from '@fortawesome/free-solid-svg-icons';
import { useNavigate, useParams } from 'react-router-dom';
import { selectNameOrders, getOrdersByName } from '../../../redux/orderReducer';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

const SavedCarts = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const orders = useSelector(selectNameOrders);
  const {name} = useParams();

  useEffect(() => {
    dispatch(getOrdersByName(name));
  }, [name]);

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-62px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome}/>
                <p className='ml-2 font-bold'>Saved Cart</p>
              </div>
              <div className='text-2xl text-gray-100 mt-8'>
                {
                  orders.map((order, index) => {
                    return (
                      <div key={index} className='w-full flex bg-[#333333] px-[2rem] py-[0.6rem] border-b border-[#444444]'>
                        <div className='w-[10%] text-left'>{index + 1}</div>
                        <div className='w-[40%] text-center'>{order.merchant.username ? order.merchant.username : order.merchant.businessname}</div>
                        <div className='w-[40%] text-center'>{order.customer.username}</div>
                        <div className='w-[10%] text-right cursor-pointer'><FontAwesomeIcon icon={faCaretDown}/></div>
                      </div>
                    );
                  })
                }
              </div>
            </div>
            <Sidebar />
        </div>
    </Layout>
  );
}

export default SavedCarts;